<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Teacher',query:{typeName,index:0}}">课程介绍</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'TeacherDetail',query:{typeName,index}}">{{menuName}}</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div id="introduce" class="introduce">
          {{setIntroduce('introduce',file.content)}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import merge from 'webpack-merge'               //用于动态修改地址栏参数的
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        List:[],                 //评价数据数组
        menus:[],                //目录数组
        menuName:'',             //菜单名
        typeName:'Teacher',      //当前菜单栏选项
        index:0,                 //第几个小菜单

        file:{},                 //当前显示评价
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //显示介绍
      setIntroduce(id,videoIntro){
        if(videoIntro){
          $('#'+id).html(videoIntro)
        }else{
          $('#'+id).html('此栏目下没有数据')
        }
      },

      //修改路由参数
      chanceUrlQuery(data){
        //清空所有的参数：
        this.$router.push({
          query:merge({},{})
        })
        //更新路由参数
        this.$router.push({
          query:merge(this.$route.query,data)
        })
      },

      //更新数据显示
      handleId(){
        var {List,menus,typeName,index} = this
        this.file = List[index]
        this.menuName = menus[index].menuName
        this.choiceMenuId(typeName)
        this.choiceTypeMenuId(typeName+index)
      },
    },
    watch: {
      //深度监听地址栏参数变化，一变化就切换对应的数据
      $route: {
        handler() {
          this.index = this.$route.query.index * 1
          this.handleId()
        },
        deep: true,
      }
    },
    mounted () {
      //页面重新加载时获取地址栏参数显示对应数据
      if(this.$route.query.typeName){
        this.typeName = this.$route.query.typeName
        this.index = this.$route.query.index * 1
      }else{
        var {typeName,index} = this
        this.chanceUrlQuery({typeName,index})
      }
      //当前栏目下的子菜单
      this.menus = [
        {
          typeName:'Teacher',
          menuName:'教师队伍结构',
          name:'TeacherDetail'
        },
        {
          typeName:'Teacher',
          menuName:'主讲教师',
          name:'TeacherOneDetail'
        },
      ],      //目录
      //当前栏目下的各个菜单包含的数据
      this.List = [
        {
          "id": "0",
          "courseName": "课程简介",
          "content":'本课程是在《动物遗传学》和《生物统计学》课程基础上，在动物科学专业课程体系中起承上启下作用。一方面它与前期所学生物统计学、动物遗传学密切相关，另一方面又是后期动物生产各论课的铺垫。本课程主要内容包括两大部分：选种和选配。通过本课程的学习，使学生掌握家畜育种的基本原理和方法，育种措施的设计和优化，育种技术的新发展及运用和家畜遗传资源的保护与利用等。为学生将来从事动物育种、动物生产和动物保护等相关行业奠定基础。',
        },
      ]
      //初始化数据
      this.handleId()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          min-height: 470px;
          padding-top: 10px;
          .introduce{
            color: #333;
            text-indent: 2em;
            font-size: 14px;
            line-height: 23px;
            min-height: 100px;
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
      }
    }
  }
</style>



